/*Multiselect Dropdown*/
form.search-form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
}
.simplecharm-multiselect-core-container{
    display: flex;
}
.simplecharm-multiselect-dropdown{
  opacity:0;
  pointer-events:none;
  position: absolute;
  top: 0;
}
.simplecharm-select-form-container{
    position: relative;
    display: flex;
}
.simplecharm-select-form-inner{
  align-self: center;
}
.simplecharm-select-form{
  width:200px;
  cursor:pointer;
  overflow:hidden;
  border: 1px solid #0a0a0a;
}
.simplecharm-select-form p{
  text-align:center;
  position:relative;
  z-index:3;
  box-shadow:2px 2px 2px #000;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.simplecharm-selectform-dropdown-icon{
  margin-left:10px;
  display:inline-block;
  transform:rotate(-90deg);
  transition:0.5s;
}
.simplecharm-selectform-dropdown-icon.multiselect-open{
  transform:rotate(0deg);
}
.simplecharm-select-options{
    width: 50%;
    transition:300ms ease;
    position: absolute;
    box-shadow: 5px 5px 5px #000;
    font-size: 1.2em;
    background: #fff;
    color: #000;
    width: 100%;
    border:1px solid #0a0a0a;
}

.simplecharm-select-options.multiselect-closed{
  height:0;
}
.simplecharm-select-form .simplecharm-select-options ul li{
  list-style:none;
  padding:5px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.simplecharm-select-options ul li:hover{
  background:rgba(0,0,0,0.2);
}
//Custom Checkbox
.simplecharm-select-options div{
    width: 20px;
    justify-self: self-end;
    position: relative;
}
.simplecharm-select-options input[type=checkbox]{
    width: 100%;
    height: 100%;
    opacity:0;
}
.simplecharm-select-options ul li div span {
    width: 100%;
    height: 20px;
    border: 1px solid #0a0a0a;
    display: block;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    position: absolute;
}
.simplecharm-select-options ul li div span::before {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    background: #009;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
}

.simplecharm-select-options ul li div input[type=checkbox]:checked~span::before {
    transform: translate(-50%, -50%) scale(1);
}

.multiselect-hide{
  opacity:0;
  pointer-events: none;
}

/*Other Styles*/
#simplecharm-loading-overlay{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0,0,0,0.5);
  justify-content:center;
  align-items:center;
  z-index:1000;
  display: none;
}

#simplecharm-loading-overlay .loader {
    height: 100px;
    width: 100px;
    border:5px solid #000;
    border-top-color:#fff;
    border-radius: 50%;
    position:fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: simplecharm-search-page-loader .5s linear infinite;
}
@keyframes simplecharm-search-page-loader{
    0%{
        transform:rotate(0deg);
    }100%{
        transform:rotate(360deg);
    }
}

/*Pagination Style*/
.simplecharm-searchpage-pagination{
    padding:20px;
    width:100%;
    text-align:center;
}
.simplecharm-searchpage-pagination a {
    padding:10px;
    border:1px solid #000;
    margin:10px;
    text-decoration:none;
}
.simplecharm-searchpage-pagination a:hover{
    background:rgba(0,0,0,0.3);
}
.simplecharm-searchpage-pagination a.active{
    background:#000;
    color:#fff;
}
.simplecharm-searchpage-pagination a.active:hover{
    opacity: 0.8;
    pointer-events: none;
}